.App {
	text-align: center;
	font-family: 'Montserrat', sans-serif;
	font-size: 16px;
	font-weight: 400;
	color: #000;
}

/* HEADER */

.App-header {
	position: relative;
	width: 100vw;
	height: 480px;
	background-image: url('../img/header-image.jpg');
	background-position: center;
	background-size: contain;
}
.overlay {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	height: 100%;
	width: 100%;
	opacity: 0.8;
	background-color: #07401A;
}
.App-title {
	font-family: 'Tangerine', cursive;
	font-size: 4em;
	font-weight: 700;
	border-bottom: solid 1px #fff;
	padding-bottom: 0.3em;
	color: #fff;
}
.App-subtitle {
	font-family: 'Montserrat', sans-serif;
	font-size: 2em;
	font-weight: bold;
	padding-top: 0.3em;
	color: #fff;
}
.text-overlay {
	position: absolute;
	width: 90%;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	    -ms-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}

/* SEARCH BUTTON */

.add-book-icon {
	color: #103210;
	font-size: 6em;
	text-shadow: 0 10px 30px #fff;
}
.add-book-icon:hover {
	cursor: pointer;
	font-size: 7em;
	-webkit-transition: all 0.3s ease;
	-o-transition: all 0.3s ease;
	transition: all 0.3s ease;
}
.submit {
	position: absolute;
	background: none;
	border: none;
	color: #00260D;
	right: 2em;
	top: 32em;
	z-index: 1;
}

/* SEARCH FORM */

.close-search {
	position: absolute;
	left: 0;
}
.close-search:hover {
	cursor: pointer;
	color: #000;
}
.arrow-back {
	font-size: 36px;
	color: rgba(0,0,0,0.5);
}
.arrow-back:hover {
	color: #000;
}
.search-form {
	position: relative;
}
.search-form {
	width: 95%;
	display: block;
	margin: 1em auto;
}
.input-label {
	font-size: 1.5em;
}
.search-input {
	width: 100%;
	border-radius: 5px;
	border: solid 1px #aaa;
	padding: 1em;
	margin-top: 1em;
	font-size: 1.5em;
	font-weight: bold;
}
::-webkit-input-placeholder {
	font-size: 1em !important;
	font-style: italic;
	font-weight: 300;
	color: #aaa;
}
:-ms-input-placeholder {
	font-size: 1em !important;
	font-style: italic;
	font-weight: 300;
	color: #aaa;
}
::-ms-input-placeholder {
	font-size: 1em !important;
	font-style: italic;
	font-weight: 300;
	color: #aaa;
}
::placeholder {
	font-size: 1em !important;
	font-style: italic;
	font-weight: 300;
	color: #aaa;
}

/* MAIN */

.container {
	width: 100%;
	max-width: 1200px;
	-webkit-box-shadow: 0 0 20px #333333;
	        box-shadow: 0 0 20px #333333;
	margin: 1em auto;
}

/* MAIN PAGE */

.tab-header {
	text-align: center;
	list-style-type: none;
	display: block;
	width: 95%;
	background-color: #185C2F;
	color: #fff;
	font-size: 1.5em;
	padding: 1em;
	margin: 0.5em auto;
	-webkit-box-shadow: 0 10px 20px #aaa;
	        box-shadow: 0 10px 20px #aaa;
}
.tab-header:hover {
	cursor: pointer;
	background-color: #4C9063;
	-webkit-box-shadow: 0 0 5px #aaa;
	        box-shadow: 0 0 5px #aaa;
	-webkit-transition: all 1s ease;
	-o-transition: all 1s ease;
	transition: all 1s ease;
}
.tab-header:active, .tab-header:focus, .tab-header::-moz-selection {
	background-color: #2E7245;
}
.tab-header:active, .tab-header:focus, .tab-header::selection {
	background-color: #2E7245;
}

/* BOOK DETAILS */
.books-list {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: center;
	    justify-content: center;
	list-style-type: none;
}
.book-item {
	margin-top: 1em;
}
.books-item {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
}
.book-container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	width: 300px;
	margin: 1em;
	-webkit-box-shadow: 0 0 10px #aaa;
	        box-shadow: 0 0 10px #aaa;
	border-radius: 5px;
}
.book-image {
	height: 220px;
	width: 140px;
	-o-object-fit: cover;
	   object-fit: cover;
}
.book-description {
	width: 160px;
	height: 200px;
}
.book-author {
	font-weight: bold;
	text-align: center;
	font-size: 1em;
	margin: 1em;
	line-height: 1.2em;
	height: 3.6em;
	position: relative;
	overflow: hidden;
}
.book-title {
	font-weight: bold;
	text-align: center;
	font-size: 1em;
	font-style: italic;
	line-height: 1.2em;
	height: 3.6em;
	margin-top: 1em;
	position: relative;
	overflow: hidden;
}
.buttons-container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	margin: 0 auto;
	width: 50%;
}
.view-details {
	color: #185C2F;
	font-size: 48px;
}
.details {
	color: #185C2F;
	font-size: 48px;
	border: none;
	background-color: rgba(0,0,0,0);
	margin-top: 0.2em;
}
.details:hover {
	text-shadow: 0 0 10px #aaa;
}
.info-icon {
	font-size: 48px;
}
.more {
	font-size: 48px;
}
.shelf-changer {
	background-image: url('../img/more-icon.svg');
	background-repeat: no-repeat;
	background-position: center;
	background-size: 48px;
}
.shelf-changer select {
	width: 100%;
	height: 100%;
	opacity: 0;
	cursor: pointer;
}
select {
	width: 200px;
	padding: 1em;
	font-family: 'Montserrat', sans-serif;
	font-size: 1em;
}

/* FOOTER */

.footer {
	background-color: #07401A;
	color: #fff;
	padding: 2em;
	margin-top: 1em;
}
.copyright {
	font-size: 1em;
	font-weight: bold;
}
.copyright a {
	color: #fff;
	text-decoration: none;
}
.copyright a:hover {
	color: #d0f091;
}

/* MODALS */

.Modal {
	position: absolute;
	top: 2%;
	left: 50%;
	-webkit-transform: translateX(-50%);
	    -ms-transform: translateX(-50%);
	        transform: translateX(-50%);
	width: 90%;
	background-color: #fff;
	-webkit-box-shadow: 0 0 20px #000;
	        box-shadow: 0 0 20px #000;
	z-index: 1000000;
}
.Overlay {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: rgba(255, 255, 255, 0.8);
	z-index: 100;
}
.close-button {
	background-color: #07401A;
	border: none;
	border-radius: 50%;
	color: #fff;
	position: absolute;
	right: -1em;
	top: -0.7em;
	height: 24px;
	-webkit-box-shadow: 0 5px 10px #000;
	        box-shadow: 0 5px 10px #000;
}
.details-container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-wrap: nowrap;
	    flex-wrap: nowrap;
}
.details-left {
	width: 90%;
	margin: 0 auto;
}
.category {
	width: 100%;
	display: block;
	font-size: 1em;
	text-align: center;
	padding: 1em;
	background-color: #000;
	color: #fff;
	margin-top: 0.5em;
}
.rating {
	display: block;
	width: 100%;
	text-align: center;
	padding: 1em;
	color: #fff;
	background-color: #07401A;
	font-size: 1em;
	font-weight: bold;
	margin-top: 0.5em;
}
.preview {
	display: block;
	width: 100%;
	padding: 1em;
	margin-top: 0.5em;
	text-align: center;
	font-size: 1em;
	font-weight: bold;
	color: #fff;
	background-color: #185C2F;
	border: none;
}
.preview a {
	color: #fff;
	text-decoration: none;
	font-size: 1em;
}
.details-image {
	width: 140px;
	height: auto;
	margin-left: 50%;
	-webkit-transform: translateX(-50%);
	    -ms-transform: translateX(-50%);
	        transform: translateX(-50%);
}
.details-right {
	width: 100%;
	padding: 1em;
}
.details-header {
	text-align: center;
	color: #185C2F;
	font-size: 1.5em;
	border-bottom: solid 1px #aaa;
	padding-bottom: 0.8em;
}
.details-subtitle {
	text-align: center;
	font-size: 1em;
	margin-top: 1em;
}
.details-table {
	margin: 1em auto;
}
.table-title {
	width: 100px;
}
.description {
	border: solid 1px #aaa;
	margin: 0 1em;
	padding: 1em;
	text-align: justify;
}

/* RESPONSIVE DESIGN */

@media screen and (min-width: 600px) {
	.tabs {
		margin-top: 1em;
	}
	.tab-header {
		display: inline-block;
		width: 45%;
		margin: 0.5em;
	}
	.details-container {
		-ms-flex-direction: row;
		    flex-direction: row;
	}
	.details-left {
		width: 140px;
	}
	.details-right {
		width: calc(100% - 140px);
	}
	.description {
		margin: 1em;
	}
}
/* Fix for Nokia Lumia and other mobiles and landscape orientation */

@media screen and (width: 640px) and (height: 360px) {
	.App-header {
		height: 360px;
	}
	.submit {
		right: 1em;
    top: 23em;
	}
}
@media screen and (min-width: 768px) {
	.App-header {
		height: 320px;
	}
	.submit {
		top: 20em;
	}
	.Modal {
		position: fixed;
		top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
	}
	.close-search {
		left: 1em;
	}
	.arrow-back {
		font-size: 48px;
	}
}
@media screen and (min-width: 900px) {
	.tab-header {
		width: 30%;
		padding: 1em;
	}
} 



